import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from './views/dashboard/Dashboard'
import ListUsers from './views/user/ListUsers'
import AddUser from './views/user/AddUser'
import ListLevels from './views/level/ListLevels'
import AddLevel from './views/level/AddLevel'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/users/list',
      name: 'ListUsers',
      component: ListUsers
    },
    {
      path: '/users/add',
      name: 'AddUser',
      component: AddUser
    },
    {
      path: '/levels/list',
      name: 'ListLevels',
      component: ListLevels
    },
    {
      path: '/levels/add',
      name: 'AddLevel',
      component: AddLevel
    }

  ]
})
